@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
.App {
    text-align: center;
    align-items: center;
    min-height: calc(100vh);
    width: 100%;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    background-color: #111;
    min-width: 285px;
}

.appBody {
    min-height: calc(100vh - 160px);
    display: block;
}

.footerContainer {
    max-width: 1000px;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

footer {
    text-align: left;
    height: 120px;
    font-size: 12pt;
    width: 100%;
}

.footerContainer > .logo {
    font-size: 20pt;
    text-decoration: none;
    margin-bottom: 5px;
    display: block;
}

a {
    color: inherit;
}

.logo {
    font-family: "Abril Fatface", cursive;
    position: relative;
    width: 100%;
    margin-top: 40px;
    font-size: 60pt;
}

.bgImage {
    background-image: url(/What-to-Drink/static/media/background_2.c3185002.jpg);
    background-position: center;
    background-size: cover;
    width: 100%;
    position: relative;
    height: 50vh;
    box-sizing: border-box;
    padding-top: 40px;
    top: 0px;
    left: 0px;
}

ul {
    list-style: none;
    padding: 0;
}

.ellipse {
    margin-top: -10px;
}

.recipeList {
    -webkit-column-count: 3;
            column-count: 3;
    -webkit-column-width: 200px;
    column-width: 200px;
    margin-top: 50px;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
}

.recipeTile {
    width: 200px;
    padding: 20px 20px;
    margin-left: 40px;
    margin-right: 40px;
    display: inline-block;
    font-size: 14pt;
    cursor: pointer;
    color: #cccccc;
    border-radius: 10px;
}
.recipeTile:hover {
    background-color: rgba(0, 0, 0, 0.7);
    color: #ffffff;
    transition: 0.1s;
}

.recipeTile > h3 {
    font-size: 16pt;
    margin-top: 5px;
}

@media only screen and (max-width: 1010px) {
    .recipeList {
        -webkit-column-count: 2;
                column-count: 2;
    }
}

@media only screen and (max-width: 600px) {
    .recipeList {
        -webkit-column-count: 1;
                column-count: 1;
    }
    .recipeList {
        margin-top: 10px;
    }
    .recipeTile {
        padding: 10px 10px;
    }
}
@media only screen and (max-width: 400px) {
    .logo {
        font-size: 40pt;
        margin-top: 0;
    }
    .bgImage {
        height: 300px;
    }
}

input {
    max-width: 450px;
    width: 90%;
    height: 55px;
    font-size: 16pt;
    background: none;
    border-radius: 15px;
    margin-top: 30px;
    color: #ffffff;
    padding-left: 20px;
    outline: none;
    border: 3px solid #ffffff;
}

::-webkit-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #bbb;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #bbb;
    opacity: 1; /* Firefox */
}

::-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #bbb;
    opacity: 1; /* Firefox */
}

::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #bbb;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #bbb;
}

::-ms-input-placeholder {
    /* Microsoft Edge */
    color: #bbb;
}

.closeIcon {
    transform: rotate(360deg);
    margin-left: -45px;
    margin-top: 46px;
    vertical-align: middle;
    cursor: pointer;
    position: absolute;
    width: 30px;
    height: 30px;
}

ul {
    padding: 0;
    margin: 10px 0px;
}

li {
    font-size: 12pt;
    margin-bottom: 10px;
}

em {
    margin-top: 10px;
    font-size: 12pt;
}

h2 {
    margin-bottom: 0px;
}

h3 {
    margin: 20px 0px 15px 0px;
}

p {
    font-size: 12pt;
    margin: 10px 0px;
}

.Recipe {
    padding-top: 40px;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

